import { Card, TextField, Typography, withStyles } from "@material-ui/core";
import clsx from "clsx";

import { Drawer, Hidden, IconButton } from "@material-ui/core";
import { ArrowBackIos } from "@material-ui/icons";

import React, { useEffect } from "react";
//import UIButton from "../../../UI/UIButton/UIButton";
import { useAuth } from "../../../contexts/AuthContext";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { acceptUserInvite } from "../../../api/Invite";

import UIButton from "../../../Components/UI/UIButton/UIButton";

interface IInviteAcceptProps {
  invitationId: string;
  invitationData: any;
}

const InviteAccept: React.FunctionComponent<IInviteAcceptProps> = (props) => {
  const { setErrorMessage } = useToastMessage();
  const { invitationId, invitationData } = props;

  const AcceptInvitation = async () => {
    const res = await acceptUserInvite(invitationId);
    console.log("res", res);
    if (res.error) {
      setErrorMessage(res.error);
    } else {
      //console.log(res.redirect);
      // window.location.href = res.redirect;
    }
  };

  return (
    <>
      <div className="invite">
        <h2 className="title">Accept Invitation</h2>
        <p>Dear {invitationData.name},</p>

        <p>
          You have been invited by {invitationData.invited_by} to join{" "}
          <strong>{invitationData.business_name}</strong> business profile on
          EasySocial. You are requested to join in and start contributing as a
          team member.
        </p>

        <p>Follow the link below to accept the invitation.</p>

        <div className={clsx("col-10 col-sm-6 col-md-12", "Submit_Container")}>
          <UIButton
            style={{ marginLeft: 0 }}
            className="mt-5 ml-1 mb-5"
            onClick={AcceptInvitation}
          >
            Accept Invitation
          </UIButton>
        </div>
      </div>
    </>
  );
};

export default InviteAccept;
