import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
  "invite-process": {
    backgroundColor: "#f3f7f9",
    display: "flex",
    flexDirection: "column",
    height: "100%",
  },

  invite: {
    backgroundColor: "#ffffff",
    borderTopLeftRadius: "24px",
    borderTopRightRadius: "24px",
    boxShadow: "0 -4px 5px 0 rgb(0 0 0 / 8%)",
    flexGrow: 1,

    "& .content": {
      height: "100%",

      "& .block-right": {
        display: "flex",
        flexDirection: "column",
        height: "100%",

        "& .form-header": {
          alignItems: "flex-start",
          display: "flex",
          padding: "15px 0 26px",

          "& .MuiIconButton-root": {
            marginLeft: "15px",
            padding: "4px",
          },

          "& .header-text": {
            margin: "auto 0",

            "& h1": {
              color: "#3b414a",
              fontSize: "24px",
              fontWeight: 500,
              lineHeight: 1.17,
            },
          },
        },

        "& .invite": {
          display: "flex",
          flexDirection: "column",
          "& .title": {
            color: "#3B414A",
            fontSize: "24px",
            fontWeight: 500,
            lineHeight: "28px",
            marginBottom: "35px",
          },
          "& p": {
            color: "#3B414A",
            fontSize: "16px",
            fontWeight: 400,
            lineHeight: "24px",
            marginBottom: "24px",
          },
        },

        "& .registerTitle": {
          color: "#3B414A",
          fontSize: "24px",
          fontWeight: 500,
          lineHeight: "28px",
          marginBottom: "35px",
        },

        "& form": {
          "& .fields": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",

            "& .form-input": {
              width: "100%",
              marginTop: "20px",

              "& label.Mui-focused": {
                color: "#000000;",
                opacity: 0.5,
              },

              "& .MuiFilledInput-root": {
                borderRadius: "8px",

                "& fieldset": {
                  borderColor: "#000000;",
                  opacity: 0.1,
                  borderRadius: "5px !important",
                },
                "&:hover fieldset": {
                  borderColor: "#000000;",
                  opacity: 0.3,
                },
                "&.Mui-focused fieldset": {
                  borderColor: "#000000;",
                  opacity: 0.3,
                },
              },

              "& .MuiFilledInput-underline:after": {
                border: "none !important",
              },

              "& .MuiFilledInput-underline:before": {
                border: "none !important",
              },

              "& input": {
                backgroundColor: "#F2F3F7!important",
                borderRadius: "8px",
                color: "#7B8AA2",

                "&:focus": {
                  border: "none",
                  boxShadow: "none",
                },
              },
            },
          },
        },

        "& .already-signed-up": {
          display: "flex",
          justifyContent: "center",
          marginTop: "auto",

          "& h3": {
            color: "#24272C",
            fontSize: "18px",
            fontWeight: 500,
            lineHeight: "21px",

            "& a": {
              color: "#12B0EE",
              textDecoration: "none",
            },
          },
        },
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    invite: {
      "& .content": {
        "& .block-right": {
          "& > .form-header": {
            margin: "0 auto",
            maxWidth: "600px",
            width: "100%",
          },

          "& > form": {
            margin: "0 auto",
            maxWidth: "510px",
            width: "100%",
          },

          "& .already-signed-up": {
            margin: "0 auto",
            maxWidth: "600px",
            width: "100%",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    "invite-process": {
      backgroundColor: "#ffffff",
    },

    invite: {
      paddingTop: "66px",
      minHeight: "max-content",

      "& .content": {
        alignItems: "center",
        display: "flex",
        height: "100%",
        justifyContent: "center",
        margin: "0 auto",
        width: "90%",

        "& .block-left": {
          height: "100%",
          display: "flex",
          justifyContent: "center",
          position: "relative",
          rowGap: "20px",
          flex: "1 0 auto",

          "& .block-fixed": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            height: "100%",
            justifyContent: "flex-start",
            position: "fixed",
          },

          "& h1": {
            color: "#3B414A",
            textAlign: "center",
            fontSize: "32px",
            fontWeight: 900,
            lineHeight: 1,
            marginTop: "85px",
          },

          "& .block-img": {
            maxWidth: "100%",
            height: "auto",
          },
        },

        "& .block-right": {
          borderLeft: "1px solid rgba(0, 0, 0, 0.1)",
          borderRight: "1px solid rgba(0, 0, 0, 0.1)",
          boxShadow: "0px 5px 40px rgba(18, 176, 238, 0.1)",
          flex: "0 1 auto",
          padding: "70px 50px",
          minHeight: "100%",
          width: "420px",

          "& .already-signed-up": {
            marginTop: 0,
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    invite: {
      "& .content": {
        gap: "90px",
        padding: "0 90px",
        width: "80%",
      },
    },
  },
  "@media(min-width:1300px)": {
    invite: {
      "& .content": {
        // gap: "180px",
        // padding: "0 180px",
        maxWidth: "1300px",
      },
    },
  },
}));
