import React, { Fragment, useEffect, useRef, useState } from "react";
import { Helmet } from "react-helmet";
import { Hidden } from "@material-ui/core";
import { useToastMessage } from "../../contexts/ToastMessageContext";
import Header from "../../Components/Header/SignUpSetup";
import { checkUserAuth } from "../../api/Auth";
import { getInvitation } from "../../api/Invite";
import image1 from "./assets/image-1.svg";

import InviteAccept from "./components/inviteAccept";

import InvitedUserRegister from "./components/InvitedUserRegister";

// styles
import useStyles from "./styles/muiStyles";
interface IInviteProps {
  match: any;
}

const Invite: React.FunctionComponent<IInviteProps> = ({ match }) => {
  const classes = useStyles();

  const { setErrorMessage } = useToastMessage();
  const [invitationData, setInvitationData] = useState(null);
  const [invitationId, setInvitationId] = useState(null);
  const [loggedIn, setLoggedIn] = useState(false);

  const getInvitationRef = useRef(async (invitationId) => {
    const authCheck = await checkUserAuth();
    authCheck.logged_in ? setLoggedIn(true) : setLoggedIn(false);

    await getInvitation(invitationId)
      .then((data) => {
        if (data.error) {
          setErrorMessage(data.error);
        }
        setInvitationData(data);
        //return cancelLoader();
      })
      .catch((err) => {
        setErrorMessage(err.message);
        //return errorHandler(err.message);
      });
  });

  useEffect(() => {
    setInvitationId(match.params.invitationId);
    getInvitationRef.current(match.params.invitationId);
  }, [match.params.invitationId]);

  /*   const checkUserExist = async () => {
    const res = await checkInvitedUserExist(match.params.invitationId);
    if (res.error) {
      setErrorMessage(res.error);
    } else {
      let redirect = `/i/${match.params.invitationId}`;
      sessionStorage.setItem("invitation", redirect);
      if (res.user === 0) {
        setInvitationData(res.data);
        setRegistered(false);
      } else {
        window.location.href = "/login";
      }
    }
  }; */

  return (
    <Fragment>
      {/* Head */}
      <Helmet>
        <title>Invitation - EasySocial.io</title>
        <link rel="canonical" href="https://easysocial.io/" />
        <meta
          name="keywords"
          content="Whatsapp chatbot ,automatic WhatsApp for business, Whatsapp AI, customizable whatsapp"
        />
        <meta
          name="description"
          content="EasySocial offers customizable and affordable WhatsApp chatbot solution for quick and hassle free functioning of your business."
        />
      </Helmet>
      <div className={classes["invite-process"]}>
        <Header />
        <section className={classes["invite"]}>
          <div className="content">
            <Hidden smDown implementation="js">
              <div className="block-left">
                <div className="block-fixed">
                  <h1>
                    {"Sign up and unbox "}
                    <br />
                    {"a pandora of marketing tools."}
                  </h1>
                  <img src={image1} alt="image1" className="block-img" />
                </div>
              </div>
            </Hidden>
            <div className="block-right">
              {loggedIn && invitationData && (
                <InviteAccept
                  invitationId={invitationId}
                  invitationData={invitationData}
                />
              )}
              {!loggedIn && invitationData && (
                <InvitedUserRegister
                  invitationId={invitationId}
                  invitationData={invitationData}
                />
              )}
            </div>
          </div>
        </section>
      </div>
    </Fragment>
  );
};

export default Invite;
